.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid whitesmoke;
}

.header_left{
    display: flex;
    justify-content: center;
}


.header_left>img{
    object-fit: contain;
    height: 50px;
    margin-left: 5px;
}


.header_middle{
    display: flex;
    flex:0.7;
    align-items: center;
    background-color: whitesmoke;
    padding: 10px;
    border-radius: 5px;
}

.header_middle> .MuiSvgIcon-root{
    color:grey;
}


.header_middle>input{
width: 50% !important;
padding: 10px;
font-size: medium;
background-color: transparent;
outline: none;
border:none;
}

.header_right{
    display: flex;
    padding-right: 20px;
    
}